/**
 * 
 */
function showMenu() {
  const itemList = this.appConfig.showMenu
  const prompt = require('@system.prompt')
  prompt.showContextMenu({
    itemList,
    success(ret) {
      if ((itemList.length - 1) === ret.index) {
        // 
        return
      }
      if (ret.index === 0) {
        // 
        createShortcut()
      } else if (ret.index === 1) {
        // app
        download()
      } else if (ret.index === 2) {
        // 
        shareWx()
      }
    }
  })
}
// 
function shareWx() {
  const share = require('@system.share')
  const prompt = require('@system.prompt')
  share.share({
    type: 'image/*',
    data: '/common/image/shareimage.png',
    success(data) {
      console.log('handling success')
    },
    fail(data, code) {
      prompt.showToast({
        message: `${code}`
      })
    }
  })
}

function download() {
  const pkg = require('@system.package')
  const prompt = require('@system.prompt')
  pkg.install({
    package: 'com.kidplay.xb',
    success(data) {
      console.log(`handling success: ${data.result}`)
    },
    fail(data, code) {
      prompt.showToast({
        message: `${code}`
      })
    }
  })
}

/**
 * 
 * ````````
 */
function createShortcut() {
  const prompt = require('@system.prompt')
  const shortcut = require('@system.shortcut')
  shortcut.hasInstalled({
    success(ret) {
      if (ret) {
        prompt.showToast({
          message: ''
        })
      } else {
        shortcut.install({
          success() {
            prompt.showToast({
              message: ''
            })
          },
          fail(errmsg, errcode) {
            prompt.showToast({
              message: `${errcode}: ${errmsg}`
            })
          }
        })
      }
    }
  })
}

function audioShow() {
  this.minplayVisible = true
  this.isplaying = false
  if (this.audio.src) {
    // 
    this.audio.onplay = () => {
      this.isplaying = true
    }
    // 
    this.audio.onpause = () => {
      this.isplaying = false
    }
    // 
    this.audio.ontimeupdate = () => {
      this.isplaying = true
    }
  }
}

function audioHide() {
  this.audio.ontimeupdate = null
  this.audio.onplay = null
  this.audio.onpause = null
  this.minplayVisible = false
}
export default {
  showMenu,
  download,
  shareWx,
  createShortcut,
  audioShow,
  audioHide
}